import verificationCodeDialog from "@/components/verificationCodeDialog.vue";
import { ref, reactive } from 'vue';
export default {
  name: "educator",
  components: {
    verificationCodeDialog
  },

  setup() {
    const profileForms = ref(null);
    const profileForm = reactive({
      password: '',
      confirm_password: '',
      current_password: ''
    });
    const profileRules = reactive({
      email_code: [{
        required: true,
        message: 'Please fill out your code.',
        trigger: 'blur'
      }],
      email: [{
        type: 'email',
        required: true,
        message: 'Please fill out your email address.',
        trigger: 'blur'
      }],
      password: [{
        required: true,
        message: 'Please enter your password',
        trigger: 'blur'
      }],
      confirm_password: [{
        required: true,
        message: 'Please enter your password again',
        trigger: 'blur'
      }]
    });
    const verificationVisible = ref(false);

    function confirmDelete() {
      verificationVisible.value = true;
    }

    return {
      profileForms,
      profileForm,
      profileRules,
      verificationVisible,
      confirmDelete
    };
  }

};